@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700&display=swap);
body{margin:0;padding:0;font-family:"Open Sans", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#7837af;background:linear-gradient(56deg, #7837af 0%, #7837af 33%, #4475af 100%)}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}::-webkit-scrollbar{width:15px}::-webkit-scrollbar-track{border-radius:100px;background-color:white}::-webkit-scrollbar-thumb{border-radius:100px;background-color:lightgray}@media (max-width: 900px){body{margin-left:20px;margin-right:20px;background:#7837af;background:linear-gradient(180deg, #7837af 0%, #7837af 33%, #4475af 100%)}}

.App{text-align:center;display:flex;align-items:center;flex-direction:column;min-height:100vh}.app-title{margin-top:90px;color:white;font-size:60px;font-weight:600}.app-subtitle1{color:white;font-size:30px;font-weight:400;margin-bottom:15px}.app-subtitle2{color:white;font-size:30px;font-weight:400;margin-top:0px;margin-bottom:80px}@media (max-width: 900px){.app-title{font-size:32px;margin-left:20px;margin-right:20px;margin-top:50px}.app-subtitle1{font-size:18px;margin-left:20px;margin-right:20px}.app-subtitle2{font-size:18px;margin-left:20px;margin-right:20px;margin-bottom:50px}}

.suggestion{cursor:pointer;border:1px}.searchInputs{display:flex}.search input{background-color:white;border:0;border-top-right-radius:10px;border-bottom-right-radius:10px;font-size:18px;padding:15px;height:30px;width:300px;color:rgba(0,0,0,0.815)}.searchIcon{height:60px;width:50px;background-color:white;display:grid;place-items:center;border-top-left-radius:10px;border-bottom-left-radius:10px;color:rgba(10,10,10,0.308)}input:focus{outline:none}.searchIcon svg{font-size:35px}.dataResult{margin-top:0px;width:380px;height:200px;background-color:white;box-shadow:rgba(0,0,0,0.35) 0px 5px 15px;overflow:hidden;overflow-y:auto;border-radius:10px;position:absolute}.dataResult::-webkit-scrollbar{width:10px}.dataResult::-webkit-scrollbar-track{border-radius:100px;background-color:white}.dataResult::-webkit-scrollbar-thumb{min-height:50px;border-radius:100px;background-color:lightgray}.dataResult .dataItem{width:100%;height:50px;display:flex;align-items:center;color:rgba(0,0,0,0.815);text-align:left;cursor:pointer}.dataItem p{margin-left:10px}.dataItem:hover{background-color:rgba(10,10,10,0.054)}#clearBtn{cursor:pointer}@media (max-width: 900px){.search input{background-color:white;border:0;border-top-right-radius:10px;border-bottom-right-radius:10px;font-size:16px;padding:15px;height:30px;width:300px;color:rgba(0,0,0,0.815)}.searchIcon{height:60px;width:50px;background-color:white;display:grid;place-items:center;border-top-left-radius:10px;border-bottom-left-radius:10px;color:rgba(10,10,10,0.308)}.searchIcon svg{font-size:30px}.dataResult{width:310px;font-size:15px}}

.summaries{display:flex;flex-direction:row;flex-wrap:wrap;margin-top:50px;justify-content:center;padding:1rem}.summary{display:flex;flex-direction:column;background-color:white;min-height:175px;overflow:hidden;height:1%;width:420px;border-radius:12px;margin:20px;box-shadow:0 15px 30px -10px rgba(0,0,0,0.25);transition:all 0.25s ease-in-out}.summary:hover{-webkit-transform:scale(1.02);transform:scale(1.02)}.button-div{display:flex;justify-content:right}.button-div .delete-button{align-self:right;text-align:right;margin-top:5px;margin-right:5px;border:none;background-color:transparent;cursor:pointer}.date{text-align:left;margin-top:0;margin-bottom:7px;margin-left:30px;font-size:13px;font-weight:500;color:rgba(0,0,0,0.815)}.location{margin-top:0;margin-left:30px;margin-bottom:7px;text-align:left;color:#7837af;font-weight:700;font-size:22px}.city{text-align:left;margin-top:0;margin-bottom:7px;margin-left:30px;font-size:16px;font-weight:400;color:rgba(10,10,10,0.671)}.values{text-align:left;margin-top:0;margin-left:30px;font-size:15px;font-weight:600;color:rgba(0,0,0,0.815)}@media (max-width: 900px){.button-div{display:flex;justify-content:left}.button-div .delete-button{align-self:right;text-align:right;margin-top:10px;margin-right:5px;border:none;background-color:transparent;cursor:pointer}.summaries{margin-top:15px}.summary{overflow:hidden;height:1%;width:340px;min-height:180px;margin-right:20px;margin-left:20px;margin-top:0px;margin-bottom:30px}.date{margin-left:17px;font-size:12px}.city{font-size:14px;margin-left:17px}.values{font-size:13px;margin-left:17px;margin-right:10px}.location{font-size:20px;margin-left:17px}}

